.sidebar {
  flex: 0.35;
}

.sidebar_search {
  display: flex;
  align-items: center;
  background-color: #e2e2e2;
  height: 39px;
  padding: 10px;
}

.sidebar_searchContainer > .MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}

.sidebar_searchContainer > input {
  border: none;
  margin-left: 10px;
}

.sidebar_searchContainer {
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 35px;
  border-radius: 20px;
}

.sidebar_header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.sidebar_headerRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
}

.sidebar_headerRight > .MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}
